*,
*::before,
*::after {
    box-sizing: border-box;
}

body {
    margin: 0;
}

ol,
ul {
    margin: 0;
    padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
}

p {
    margin: 0;
}

img {
    display: block;
    max-width: 100%;
    height: auto;
}

button {
    outline: none;
}

svg {
    display: block;
}