:root {
  --brend-color: #e86f54;
  --white-color: #fff;
  --black-color: #000;
  --logo-color: #0e0e0e;
  --modal-text-color: #5c5c5c;
  --modal-input-color: #d7d7d7;
  --button-hover-color: #e25b41;
  --button-focus-color: #fa6f54;
  --button-blocked-color: rgba(232, 111, 84, .4);
  --function-animation: cubic-bezier(.4, 0, .2, 1);
}

.modal-follow, .backdrop-follow, .modal-container, .backdrop-contact, .hero__button-svg {
  transition-duration: .25s;
  transition-timing-function: var(--function-animation);
}

.footer__container, .blog, .delivery__container, .store, .about__container, .hero__section, .header__container {
  margin: 0 auto;
}

@media screen and (max-width: 767.98px) {
  .footer__container, .blog, .delivery__container, .store, .about__container, .hero__section, .header__container {
    max-width: 480px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

@media screen and (min-width: 768px) {
  .footer__container, .blog, .delivery__container, .store, .about__container, .hero__section, .header__container {
    width: 768px;
    padding-left: 24px;
    padding-right: 24px;
  }
}

@media screen and (min-width: 1280px) {
  .footer__container, .blog, .delivery__container, .store, .about__container, .hero__section, .header__container {
    width: 1280px;
  }
}

@media screen and (max-width: 767.98px) {
  .blog, .section__delivery, .store, .section__about {
    padding-bottom: 60px;
  }
}

@media screen and (min-width: 768px) {
  .blog, .section__delivery, .store, .section__about {
    padding-bottom: 100px;
  }
}

.contact-form__list, .footer-social__list, .footer-address__list, .footer-nav__list, .store__list, .mobile__list {
  list-style: none;
}

.footer-address__location, .footer-address__addr, .footer-nav__link, .mobile__link {
  color: inherit;
  text-decoration: none;
  display: block;
}

.blog__title, .delivery__title, .store__title, .about__title {
  text-transform: uppercase;
  color: var(--black-color);
  align-items: center;
  font-family: Montserrat-Bold, sans-serif;
  display: flex;
}

.blog__title:after, .delivery__title:after, .store__title:after, .about__title:after {
  content: "";
  width: 100%;
  height: 1px;
  background-color: var(--black-color);
  display: block;
}

@media screen and (max-width: 767.98px) {
  .blog__title:after, .delivery__title:after, .store__title:after, .about__title:after {
    margin-left: 14px;
  }
}

@media screen and (min-width: 768px) {
  .blog__title:after, .delivery__title:after, .store__title:after, .about__title:after {
    margin-left: 16px;
  }
}

@media screen and (max-width: 767.98px) {
  .blog__title, .delivery__title, .store__title, .about__title {
    font-size: 24px;
    line-height: 1.20833;
  }
}

@media screen and (min-width: 768px) {
  .blog__title, .delivery__title, .store__title, .about__title {
    font-size: 44px;
    line-height: 1.22727;
  }
}

.blog__button, .store__button {
  background-color: var(--brend-color);
  color: var(--white-color);
  border: none;
  border-radius: 38px;
  font-family: Montserrat-Medium, sans-serif;
}

@media screen and (max-width: 767.98px) {
  .blog__button, .store__button {
    padding: 10px 35px;
    font-size: 20px;
    line-height: .833333;
  }
}

@media screen and (min-width: 768px) {
  .blog__button, .store__button {
    padding: 20px 69.5px;
    font-size: 28px;
    line-height: .857143;
  }
}

.blog__button:hover, .store__button:hover {
  background-color: var(--button-hover-color);
}

.blog__button:focus, .store__button:focus {
  background-color: var(--button-focus-color);
}

