.hero__button {
  cursor: pointer;
  animation-name: rotate-bt;
  animation-duration: 3s;
  animation-timing-function: var(--function-animation);
  background-color: rgba(0, 0, 0, 0);
  border: none;
  padding: 0;
  animation-iteration-count: infinite;
  animation-play-state: running;
  transform: scale(1);
}

